*{
    margin:0;
    padding:0;
}
.App img{
    width: 100%;
    height: 100%;
}
.App {
    position: relative;
}
.App-blocks{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    font-size: 0;
}
.App-block-item{
    display: inline-block;
    border: 1px solid #000;
    border-width: 0 0 1px 1px;
    box-sizing: border-box;
}
.App-block-item:hover {
    background: none !important;
}
.App-toolbar{
    position: fixed;
    right: 10px;
    bottom: 10px;

    button {
        margin-right: 1rem;
        padding: 10px 30px;
    }
}