/*map*/
html, body, #root{
    overflow: auto;
}
#App-phaser-parent {
    display: inline-block;
/*    background: linear-gradient(rgba(200, 200, 200, 0.1), rgba(0, 0, 0, 0.1)), url(assets/sea.jpg) no-repeat 0% 20%/ cover;*/
    margin: 50px;
}
#App-phaser-preview canvas{
    width: 100%;
}
.App-map-preview{
    canvas{
        width: 100%;
        height: 100%;
    }
}
.App-block-panel{
    display: flex;

    .App-Resources-content{
        width: 800px;
    }
    .App-block-panel-cod{
        margin: 20px 0 20px 20px;
        width: 480px;
        height: 360px;
    }
}

.App-game-modal-content-desc{
    padding: 10px 20px 15px;
    text-align: justify;
}
.App-preview-map-btn{
    z-index: 9;
    padding-top: 4.5rem;
    background: url(./assets/preview.png) no-repeat 0 0;
    background-size:100% auto;
    width: 5rem;
    cursor: pointer;
    span.text{
        display: block;
        background: rgba(0,0,0,0.3);
        border-radius: 15px;
        text-align: center;
        font-size: 14px;
        color:#fff;
    }
}
.App-morale-map-btn{
    background: url(./assets/morale.png) no-repeat 0 0;
}
.App-preview-rank-btn{
    padding-top: 65px;
    background: url(./assets/preview.png) no-repeat 0 140%;
    background-size:100% auto;
}
.App-chat-map-btn{
    background: url(./assets/chat.png) no-repeat center 0;
    background-size:80% auto;
}

// stat
.App-Resources-stat-title{
    font-weight: 700p;
    font-size: 14px;
    margin: 20px 0 10px 5px;   
    color: rgb(240, 192, 103);
}
.App-Resources-stat-list {
    display: inline-block;
    margin-right: 20px;
}
.App-Resources-stat-item {
    background: #1c140b;
    border-radius: 20px;
    margin-bottom: 10px;
    font-size: 12px;
    padding:4px;
    min-width: 170px;

    dt,dd{
        margin-left: 40px;
    }
    dt{
        color:#c4b596;
    }
    dd{
        font-weight: 14px;
        color:#fff8ca;
    }
}
.App-Resources-tile {
    display: inline-block;
    width: 200px;
    text-align: center;
}
.App-Resources-tile img{
    width: 90%;
    vertical-align: baseline;
}

// troops
.App-Resources-troops {
    width:400px;
}

// lasting
.App-Resources-lasting{
    font-size: 12px;
    display: inline-block;
    width: 350px;
    margin-right: 2rem;
    position: relative;
    line-height: 1.4;
}
.App-Resources-lasting-title{
    color: #f0c067;
}
.App-Resources-lasting-tips{

}
.App-Resources-lasting-text{
    margin: -10px 0 0 370px;
    white-space: nowrap;
    text-align: center;
    width: 160px;
}


.App-game-modal-block{
    .App-block-panel,.App-block-detail{
        width: 100%;
    }
    .App-game-modal-header{
        width: 100%;
    }
    .App-block-types{
        width: 100%;
        margin: 0;
        padding: 10px;
        background: url(./assets/hr.png) no-repeat 0 100%;
    }
}

.App-single-tile{
    zoom: 0.7;
    margin: 30px 0 0 30px;
}


.App-game-map-list{
    min-height: 100px;
    max-height: 170px;
    overflow: hidden;
}
.App-game-map-list:hover{
    overflow: auto;
}
.App-game-map-title{
    background: url(./assets/splitter.png) no-repeat 0 100%;
    background-size: 100% auto;
    padding: 0 0 10px 2px;
    margin: 10px 10px 0;
    color: rgba(240, 192, 103, 1);
    font-size: 20px;
}
.App-game-map-item{
    display: flex;
    background: #1E160C;
    border: 1px solid #1E160C;
    padding: 8px 10px;
    margin: 5px 10px 0;
    border-radius: 6px;
    .name,
    .vote{
        display: inline-block;
        width: 50%;
    }
    .vote{
         text-align: right;
         margin-right: 30px;
         width: 30%;
    }
    .name{
        opacity: 0.8
    }
}
.App-game-map-item-selected,
.App-game-map-item:hover{
    border: 1px solid #443727;
    color: rgba(240, 192, 103, 1);
    .name{
        opacity: 1;
    }
}
.App-map-vote-desc{
    margin: 20px;
    p{
        margin-bottom: 5px;
    }
}

.App-seaon-map-canvas{
    width: 100%;
    height: 105%;
    canvas{
        width: 100%;
        height: 100%;
        margin: 0;
    }
    img{
        width: 100%;
        max-height: 100%;
    }
}

.App-game-map-view-main{
    width: 600px;
    height: 390px;
    .App-seaon-map-canvas{
        height: 390px;
    }
    canvas{
        width: 100%;
        height: 100%;
        margin: 0;
    }
}

.App-zoom-btn{
    span{
        font-size: 2rem;
        margin-right: 1rem;
        background: #ccc;
        padding: 10px 30px;
    }
}